html {
    box-sizing: border-box;
    height: 100%;
}

*,
*::before,
*::after {
    box-sizing: inherit;
}

body {
    background-color: $darkColor;
    color: #fff;
    font-family: $fontFamily;
    font-weight: $fontWeight;
    font-size: clamp(14px, 1.32vw, $fontSize);
    line-height: $lineHeight;
    height: 100%;

    &.ov-hidden {
        overflow: hidden;
    }
}

a {
    text-decoration: none;
    transition: color .3s;
}

img {
    max-width: 100%;
    height: auto;
    vertical-align: middle;
}

p {
    margin: 1.5em 0;

    &:first-child {
        margin-top: 0;
    }

    &:last-child {
        margin-bottom: 0;
    }
}

section {
    position: relative;
}

.page-container {
    max-width: $containerWidth;
    margin-left: auto;
    margin-right: auto;
    padding-left: 15px;
    padding-right: 15px;
    width: 95%;

    @include min-w($screen-sm) {
        padding-left: $containerPadding;
        padding-right: $containerPadding;
    }

    @include min-w($screen-macbook) {
        width: 90%;
    }

    @include min-w(2080px) {
        max-width: $containerWidth + 200;
    }
}
