@font-face {
    font-family: 'Raleway';
    font-display: swap;
    font-weight: normal;
    src: local('Raleway'), local('Raleway-Regular');
    src: url('../fonts/Raleway-Regular.woff2') format('woff2'),
        url('../fonts/Raleway-Regular.woff') format('woff'),
        url('../fonts/Raleway-Regular.ttf') format('truetype');
}

@font-face {
    font-family: 'Raleway';
    font-display: swap;
    font-weight: 500;
    src: local('Raleway'), local('Raleway-Medium');
    src: url('../fonts/Raleway-Medium.woff2') format('woff2'),
        url('../fonts/Raleway-Medium.woff') format('woff'),
        url('../fonts/Raleway-Medium.ttf') format('truetype');
}

@font-face {
    font-family: 'Raleway';
    font-display: swap;
    font-style: italic;
    font-weight: normal;
    src: local('Raleway'), local('Raleway-Italic');
    src: url('../fonts/Raleway-Italic.woff2') format('woff2'),
        url('../fonts/Raleway-Italic.woff') format('woff'),
        url('../fonts/Raleway-Italic.ttf') format('truetype');
}

@font-face {
    font-family: 'Raleway';
    font-display: swap;
    font-weight: 600;
    src: local('Raleway'), local('Raleway-SemiBold');
    src: url('../fonts/Raleway-SemiBold.woff2') format('woff2'),
        url('../fonts/Raleway-SemiBold.woff') format('woff'),
        url('../fonts/Raleway-SemiBold.ttf') format('truetype');
}

@font-face {
    font-family: 'Raleway';
    font-display: swap;
    font-weight: 700;
    src: local('Raleway'), local('Raleway-Bold');
    src: url('../fonts/Raleway-Bold.woff2') format('woff2'),
        url('../fonts/Raleway-Bold.woff') format('woff'),
        url('../fonts/Raleway-Bold.ttf') format('truetype');
}

@font-face {
    font-family: 'OpenSans';
    font-display: swap;
    font-weight: normal;
    src: local('OpenSans'), local('OpenSans-Regular');
    src: url('../fonts/OpenSans-Regular.woff2') format('woff2'),
        url('../fonts/OpenSans-Regular.woff') format('woff'),
        url('../fonts/OpenSans-Regular.ttf') format('truetype');
}

@font-face {
    font-family: 'OpenSans';
    font-display: swap;
    font-weight: 500;
    src: local('OpenSans'), local('OpenSans-Medium');
    src: url('../fonts/OpenSans-Medium.woff2') format('woff2'),
        url('../fonts/OpenSans-Medium.woff') format('woff'),
        url('../fonts/OpenSans-Medium.ttf') format('truetype');
}

@font-face {
    font-family: 'OpenSans';
    font-display: swap;
    font-weight: 700;
    src: local('OpenSans'), local('OpenSans-Bold');
    src: url('../fonts/OpenSans-Bold.woff2') format('woff2'),
        url('../fonts/OpenSans-Bold.woff') format('woff'),
        url('../fonts/OpenSans-Bold.ttf') format('truetype');
}


h1,
h2,
h3,
h4,
h5 {
    font-family: $fontRaleway;
    font-weight: 600;
    line-height: 1.3;
}

h1 {
    font-size: clamp(40px, 5vw, 94px);
}

.section-title {
    font-size: clamp(34px, 3.8vw, 52px);
    margin: 0 0 .7em;
}

h2 {
    @extend .section-title;
}

h3 {
    font-size: clamp(20px, 1.8vw, 24px);
}