.hero {
    padding: 90px 0 100px;
    display: flex;
    align-items: center;
    overflow: hidden;

    @media screen and (min-height: 580px) {
        height: 100vh;
    }

    &__blur {
        position: absolute;
        top: 200px;
        left: -5%;
        background-color: $greenColor;
        opacity: .2;
        height: 150px;
        width: 150px;
        border-radius: 40%;
        filter: blur(70px);
        -webkit-backface-visibility: hidden;
        -moz-backface-visibility: hidden;
        -webkit-transform: translate3d(0, 0, 0);
        -moz-transform: translate3d(0, 0, 0);
        z-index: -1;
        animation: wander 6s infinite alternate, resize-small 5s infinite alternate;

        @include min-w($screen-md) {
            animation: wander 6s infinite alternate, resize 5s infinite alternate;
            height: 200px;
            width: 200px;
        }
    }

    &__blur-2 {
        @extend .hero__blur;
        background-color: $blueColor;
        top: 100px;
        left: 15%;
        height: 250px;
        width: 250px;
        animation: wander2 7s infinite alternate, resize-small 5s infinite alternate;

        @include min-w($screen-md) {
            animation: wander2 7s infinite alternate, resize 5s infinite alternate;
        }
    }

    &__container {
        position: relative;
    }

    &__scroll-down {
        position: absolute;
        padding-top: 23px;
        left: 50%;
        bottom: 0;
        display: flex;
        align-items: flex-start;
        justify-content: center;
        transform: translate(-50%, 50%);
        border-radius: 300px;
        height: 150px;
        width: 150px;
        background: linear-gradient($darkColor, $darkColor) padding-box, 
            linear-gradient(to bottom, rgba(255, 255, 255, 0.2), rgba(171, 171, 171, 0.04)) border-box;
        border: 1px solid transparent;
        z-index: 1;

        @include min-w($screen-md) {
            padding-top: 39px;
            height: 200px;
            width: 200px;
        }

        a {
            transition: all .4s;

            &:hover {
                transform: translateY(5px);
            }
        }
    }
}

.page-title {
    font-weight: $fontWeight;

    @media screen and (min-width: $screen-md) and (max-width: $screen-laptop) and (min-height: $screen-laptop) {
        font-size: clamp(58px, 7.3vw, 75px);
        margin-bottom: 1em;
    }

    span {
        color: $primaryColor;
    }
}

.creeping-line {
    display: inline-block;
    max-height: 1em;
    overflow: hidden;
    margin: 0 20px -10px 0;

    ul {
        @include clearlist;
        animation: move 9s linear infinite;

        li {
            color: $primaryColor;
            line-height: 1.2;
            white-space: nowrap;
        }
    }
}

@keyframes move {
    0% {
        transform: translateY(0);
    }

    100% {
        transform: translateY(calc(-50%));
    }
}

@keyframes wander {
    40%, 50% {
        top: 0;
        left: 0;
    }

    100% {
        top: 15px;
        left: 30%;
    }
}

@keyframes wander2 {
    60%, 70%  {
        top: 80px;
        left: 50%;
    }

    95%, 100% {
        top: 0;
        left: 40%;
    }
}

@keyframes resize {
    50% {
        height: 180px;
        width: 180px;
    }

    100% {
        height: 240px;
        width: 240px;
    }
}

@keyframes resize-small {
    50% {
        height: 120px;
        width: 120px;
    }

    100% {
        height: 180px;
        width: 180px;
    }
}