.page-header {
    background-color: $darkColor;
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    align-items: center;
    width: 100%;
    height: 85px;
    z-index: $menuZindex;
    transition: top .3s;

    &--light {
        background-color: transparent;
    }

    &__inner {
        display: flex;
        align-items: center;
        justify-content: space-between;

        > .main-nav {
            display: none;

            @include min-w($screen-md) {
                display: flex;
            }
        }
    }

    &__name {
        font-size: 1.25em;
    }

    a {
        color: #fff;
    }

    &__burger {
        display: flex;
        align-items: center;
        gap: .5rem;
        font-weight: 600;
        color: #fff;
        height: 18px;
        position: relative;
        cursor: pointer;

        @include min-w($screen-md) {
            display: none;
        }
    }

    &__nav-icon {
        cursor: pointer;
        height: 18px;
        position: relative;
        transform: rotate(0deg);
        transition: .4s ease-in-out;
        width: 29px;

        span {
            background: #fff;
            height: 2px;
            opacity: 1;
            position: absolute;
            left: 0;
            transform-origin: center;
            transform: rotate(0deg);
            width: 100%;

            &:first-child {
                top: 0;
            }

            &:nth-child(2) {
                top: calc(50% - 1px);
            }

            &:last-child {
                bottom: 0;
            }
        }
    }

    &__mobile-menu {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 0;
        position: absolute;
        top: 0;
        right: 0;
        overflow: hidden;
        transition-delay: .5s;

        @include min-w($screen-md) {
            display: none;
        }

        &:before {
            background-color: rgba(2, 4, 22, .7);
            backdrop-filter: blur(15px);
            content: '';
            height: 0;
            position: absolute;
            top: 0;
            transition: height .75s ease;
            width: 100%;
        }

        &.animated {
            height: 100dvh;
            transition: all 0s;

            &:before {
                height: 100%;
            }

            .page-header__mobile-menu-inner {
                opacity: 1;
                transition-delay: .4s;
            }
        }

        .page-header__name {
            font-size: 26px;
        }
    }

    &__mobile-menu-inner {
        width: 100%;
        height: 100%;
        overflow: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-around;
        opacity: 0;
        transition: opacity .6s ease;
        z-index: $menuZindex;
    }

    &__close-mobile-menu {
        font-size: 18px;
        font-weight: 600;

        img {
            margin-left: 10px;
        }
    }
}

.main-nav {
    &__list {
        display: flex;
        flex-direction: column;
        align-items: center;
        @include clearlist;
        gap: 40px;

        @include min-w($screen-md) {
            flex-direction: row;
        }

        @media screen and (max-height: 400px) and (max-width: $screen-md) {
            gap: 20px;
        }

        a {
            font-size: 24px;

            @include min-w($screen-md) {
                font-size: 1.1em;
            }
        }
    }
}