// Buttons

.btn {
    font-family: $fontRaleway;
    font-size: clamp(14px, 1.25vw, 16px);
    font-weight: 600;
    display: inline-block;
    vertical-align: middle;
    background: none;
    outline: 0;
    border: 0;
    border-radius: 0;
    padding: 18px 74px 17px 17px;
    background-color: $primaryColor;
    border-right: 2px solid $primaryColor;
    border-radius: 6px;
    text-transform: uppercase;
    position: relative;
    overflow: hidden;
    color: #fff;
    cursor: pointer;
    min-width: 177px;
    box-shadow: 0 10px 20px rgba(100, 100, 200, .2);

    @include min-w($screen-md) {
        padding: 22px 88px 21px 21px;
    }

    &::before,
    &::after {
        content: '';
        display: inline-block;
        position: absolute;
    }

    &::before {
        width: 70px;
        background: $darkColor;
        height: calc(100% - 4px);
        top: 2px;
        right: -15px;
        transform: skew(23deg);
        transition: .3s;
        z-index: 1;

        @include min-w($screen-md) {
            width: 80px;
        }
    }

    &::after {
        background: url('../../icons/arrow-right.svg') no-repeat 100%;
        top: 50%;
        right: 23px;
        height: 13px;
        width: 18px;
        transform: translateY(-50%);
        z-index: 2;
    }

    &:hover {
        text-decoration: none;
        
        &::before {
            right: -100px;
        }
    }

    &--simple {
        padding: 18px 13px 17px 13px;
        background: #fff;
        border: 1.5px solid $primaryColor;
        color: $primaryColor;
        overflow: hidden;
        transition: color .4s;

        @include min-w($screen-md) {
            padding: 20px 15px 19px 15px;
        }

        &::after,
        &::before {
            display: none;
        }

        .background {
            background-color: $primaryColor;
            width: 400px;
            height: 400px;
            position: absolute;
            border-radius: 50%;
            left: -210px;
            top: -175px;
            transform: scale(0);
            transition: transform .4s;
            z-index: 1;
        }

        .text {
            position: relative;
            z-index: 2;
        }

        &:hover {
            background: $primaryColor;
            color: #fff;
            transition:  background .1s .3s;

            .background {
                transform: scale(1);
            }
        }
    }
}