.results-section {
    padding: clamp(70px, 7vw, 100px) 0;
    background: linear-gradient(to right, #0d0d2b, #0a0a23);
    color: #fff;
  
    &__grid {
      display: grid;
      grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
      gap: 40px;
      text-align: center;

      @include min-w($screen-md) {
        grid-template-columns: repeat(4, 1fr);
        gap: clamp(20px, 2.85vw, 40px);
      }
    }
  
    &__result {
      h3 {
        font-size: clamp(28px, 3vw, 42px);
        margin: 0 0 .75em;
        line-height: 1;
        //color: #4f9dff;
        color: #fff;
      }
  
      p {
        margin-top: 0;
        opacity: .8;
      }
    }
  }
  