.about-section {
    padding: clamp(60px, 5.75vw, 80px) 0;
    background-color: $lightColor;
    color: $textColor;

    &__title {
      margin-bottom: 0;
    }

    &__grid {
      display: grid;
      grid-template-columns: 1fr;
      gap: 25px;

      @include min-w($screen-md) {
        gap: 50px clamp(30px, 3.5vw, 50px);
        grid-template-columns: 1fr 1.5fr;
      }
    }

    &__skills {
      margin-bottom: 0;
  }
}