.footer {
    text-align: center;

    a {
        background: inherit;
        padding: clamp(80px, 11vw, 150px) 0;
        display: block;
        color: #fff;

        &:hover {
            animation: gradient 5s ease infinite alternate;
            background: linear-gradient(-45deg, #c0e449, #1b31c2, #020416, #101010);
            background-size: 400% 400%;
        }
    }

    p {
        font-size: 1.2em;
    }
}

@keyframes gradient {
    0% {
        background-position: 0 50%;
    }
    
    100% {
        background-position: 100% 50%;
    }
}