.skills-section {
    padding: clamp(60px, 5vw, 70px) 0;
    background-color: $lightColor;
    color: $textColor;
  
    &__title {
      text-align: center;
    }
  
    &__groups {
      display: grid;
      grid-template-columns: 1fr;
      gap: 30px;

      @include min-w($screen-x-sm) {
        grid-template-columns: repeat(2, 1fr);
        gap: clamp(40px, 5.3vw, 80px);
      }

      @include min-w($screen-laptop) {
        grid-template-columns: .5fr .25fr .25fr;
      }

      @include min-w($screen-macbook) {
        grid-template-columns: .44fr .28fr .28fr;
      }
    }
  
    &__group {
      h3 {
        margin: 0 0 1em;
        //color: #444;
      }

      &--using-now {
        @include min-w($screen-x-sm) {
          grid-column: 1/4;
        }

        @include min-w($screen-laptop) {
          grid-column: auto;
        }
      }
    }

    &__list {
      display: flex;
      flex-wrap: wrap;
      gap: clamp(15px, 2vw, 30px);
      padding-left: 0;

      li {
        display: flex;
        align-items: center;
        gap: 0.5rem;
        font-weight: 500;
        color: #333;
      }
    }

    &__icon {
        font-size: 1.3em;
        color: #4f9dff;
    }
  }
  