.portfolio {
    background-color: #fff;
    color: $textColor;
    padding: clamp(50px, 6vw, 80px) 0 clamp(70px, 7.3vw, 100px);
    text-align: center;

    &__list {
        display: grid;
        gap: clamp(20px, 2.2vw, 30px);
        grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
        @include clearlist;

        @include min-w($screen-x-sm) {
            grid-template-columns: repeat(2, 1fr);
        }

        @include min-w($screen-laptop) {
            grid-template-columns: repeat(3, 1fr);
        }
    }

    &__item {
        border-radius: 5px;
        box-shadow: 0 15px 25px rgba(131,114,200,.2);
        cursor: pointer;
        text-align: left;

        @media (hover: hover) {
            &:hover {
                .portfolio__thubnail-wrap {
                    padding: 0;
    
                    // &::after {
                    //     opacity: 1;
                    //     padding: 0;
                    // }
                }
            }
        }
    }

    &__thubnail-wrap {
        padding: 20px 20px 0;
        transition: padding .4s;
        height: auto;
        overflow: hidden;
        position: relative;

        @include min-w($screen-x-sm) {
            height: clamp(180px, 29vw, 300px);
        }

        @include min-w($screen-laptop) {
            padding: 25px 25px 0;
            height: clamp(210px, 17.85vw, 257px);
        }

        // &::after {
        //     background: url('../../../public/images/radial-gradient.png') no-repeat right top;
        //     background-origin: content-box;
        //     background-size: cover;
        //     display: block;
        //     height: 100%;
        //     width: 100%;
        //     content: '';
        //     position: absolute;
        //     padding: 25px 25px 0;
        //     top: 0;
        //     left: 0;
        //     opacity: 0;
        //     transition: opacity .4s, padding .4s;
        // }
    }

    &__thubnail-inner {
        border-radius: 5px;
        overflow: hidden;
        height: 100%;
    }

    &__thubnail {
        aspect-ratio: 4 / 3;
        object-fit: cover;
        object-position: 0 0;
        width: 100%;
    }

    &__img {
        @extend .portfolio__thubnail;
        aspect-ratio: 16 / 9;
    }

    &__legend {
        padding: 20px 25px 25px; 
        transition: padding .4s;
    }

    &__title {
        margin: 0 0 .5em;
    }

    &__text {
        margin-top: 1em;
    }

    &__load-more {
        text-align: center;
        margin-top: 40px;
    }
}

.slick-prev {
    width: 20px;
    height: 20px;
    left: -38px;
    z-index: 10;

    @include min-w($screen-md) {
        width: 26px;
        height: 26px;
        left: -46px;
    }

    &::before {
        font-size: 26px;
        padding: 10px;
        opacity: 1;
        color: #fff;
    }

    &.slick-disabled {
        &::before {
            opacity: .5;
        }
    }
}

.slick-next {
    @extend .slick-prev;
    left: auto;
    right: -12px;

    @include min-w($screen-md) {
        right: -26px;
    }
}

.slick-dots {
    bottom: -30px;

    li {
        button {
            &::before {
                color: $greenColor;
                opacity: .7;
                font-size: 13px;

                @include min-w($screen-md) {
                    font-size: 10px;
                }
            }
        }

        &.slick-active {
            button {
                &::before {
                    font-size: 16px;
                    color: $greenColor;
                    opacity: 1;

                    @include min-w($screen-md) {
                        font-size: 12px;
                    }
                }

                &:hover {
                    &::before {
                        opacity: 1;
                    }
                }
            }
        }
    }
}