.modal {
    display: block;
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    overflow: auto;
    background-color: rgba(0, 0, 0, .85);
    z-index: $modalZindex;

    &-content {
        animation-duration: .4s;
        animation-name: zoom;
        display: block;
        left: 50%;
        max-width: 900px;
        position: absolute;
        top: 50%;
        // background: linear-gradient($darkColor, $darkColor) padding-box, 
        // linear-gradient(to bottom, rgb(90, 90, 90), rgb(200, 200, 200)) border-box;
        border-radius: 4px;
        transform: scale(1) translate(-50%, -50%);
        transform-origin: left;
        width: 90%;

        @include min-w($screen-md) {
            width: 75%;
        }
    }

    &-close {
        cursor: pointer;
        margin-left: auto;
        width: 28px;
        height: 28px;
        display: flex;
        align-items: center;
        justify-content: center;
        position: absolute;
        top: -14px;
        right: -14px;
        background-color: #fff;
        color: $blueColor;
        border-radius: 50%;
        text-align: center;
        font-size: clamp(26px, 2.5vw, 34px);
        line-height: 1.5;
        transition: opacity .3s;
        z-index: $modalZindex;
        -webkit-user-select: none;
        user-select: none;

        @include min-w($screen-md) {
            top: -18px;
            right: -18px;
            width: 36px;
            height: 36px;
        }

        &:hover {
            opacity: .9;
        }
    }
}

@keyframes zoom {
    0% {
        transform: scale(0) translate(-50%, -50%)
    }

    to {
        transform: scale(1) translate(-50%, -50%)
    }
}